/* =====================================================================
   styles.scss — TF Slider (배경/오버레이) + Minecraft Menu + 거래소 버튼
   ===================================================================== */

@use '../../scss/' as *;

/* ---------------------------
   공통: 슬라이더 배경 & 오버레이
---------------------------- */
.tf-slider {
  background: url('../../assets/images/OrcaXP/bg.png') center center no-repeat;
  background-size: cover;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    background-color: transparent;
    z-index: 0;
  }

  .bg-inner {
    position: absolute;
    inset: 0;
    z-index: 1;
    mix-blend-mode: overlay;
    pointer-events: none;
    animation: moveInner 15s ease infinite alternate;
    opacity: 0.85;
  }

  .container-fluid { position: relative; z-index: 2; }

  // @keyframes moveInner {
  //   0%   { background-color: #f0f; }
  //   25%  { background-color: #0f0; }
  //   50%  { background-color: #00f; }
  //   75%  { background-color: rgb(255, 170, 0); }
  //   100% { background-color: #ff0; }
  // }

  &.home2 {
    background: url('../../assets/images/slider/bg-slider-2.jpg') center center no-repeat;
    background-size: cover;
  }
  &.home3 {
    background: url('../../assets/images/slider/bg-slider-3.jpg') center center no-repeat;
    background-size: cover;
  }
}

/* ---------------------------
   레이아웃 컨테이너
---------------------------- */
.tf-slider-item {
  @include flexitem;
  padding: 50px 3% 50px 10.5%;

  @include desktop-1650 { padding: 50px 3% 50px 10.5%; }

  .content-inner {
    width: 50%;
    position: relative;
    padding-top: 80px;
    max-width: 800px;
    margin-right: 0;
    text-align: left;

    .text-typing { display: flex; align-items: center; gap: 16px; }

    .heading {
      color: #fff;
      margin-bottom: 19px;
      &.mb0 { margin-bottom: 0; }
    }

    .animate-contain {
      height: 80px; overflow: hidden; text-align: left; margin-left: 20px;
    }

    .animated-text {
      display: inline-block;
      transition: 300ms all;
      margin-top: 0;
      animation: slide 5s infinite alternate ease-in-out;
      color: var(--primary-color5);
      height: 100%;
    }

    .animated-text span {
      display: block;
      font-size: 60px; font-weight: 400; height: 100%;
      display: flex; align-items: center;
      font-family: "Bakbak One", sans-serif;
      text-transform: uppercase;

      @include mobile { font-size: 50px; line-height: 60px; }
    }

    @keyframes slide {
      0% { margin-top: 0; } 20% { margin-top: 0; }
      40% { margin-top: -80px; } 60% { margin-top: -80px; }
      80% { margin-top: -160px; } 100% { margin-top: -160px; }
    }

    .sub-heading { color:#fff; font-size:20px; line-height:30px; margin-bottom:48px; }

    .tf-button {
      padding: 11px 24px; margin-right: 20px;
      @include w-402 { width: 70%; margin-right: 0; }
      &.style-2 { padding: 11px 22px; color: #fff; }
    }
  }

  .image {
    flex-grow: 1; width: 50%; display: flex;
    img { margin-right: 18px; border-radius: 12px; width: 33.33%; &:last-child{margin-right:0;} }
  }

  .content-right {
    .content-slide {
      @include flexitem;
      margin-left: 0; height: 480px; overflow: hidden; justify-content: center;
      .mr25 { margin-right: 25px; }

      .image {
        width: 1000px; max-width: 90%; border-radius: 20px; overflow: hidden;
        margin-left: 0; position: relative; overflow: hidden;

        img { position: static; top: 0; left: 0; width: 100%; height: 100%; object-fit: cover; }

        &.s1 { width: 266px; }

        @keyframes fadeIn { 0%{opacity:0;} 40%{opacity:0;} 60%{opacity:1;} 100%{opacity:1;} }
        .fadeInClass {
          animation-name: fadeIn; animation-duration: 7s; animation-iteration-count: infinite;
          animation-direction: alternate; animation-timing-function: linear;
        }

        @include desktop-1650 { width: 90%; }
      }
    }
  }

  /* === 여기만 변경됨: 풀스크린 배너 === */
  &.style-2 {
    min-height: 80vh;           // 화면 높이 가득
    display: flex;
    align-items: center;          // 수직 가운데
    justify-content: center;      // 수평 가운데
    flex-direction: column;
    padding: 50px 0;              // 위/아래 여백만 살짝
    text-align: center;

    .content-inner {
      width: 100%;
      max-width: unset;
      margin-right: 0;
      padding-top: 0;
    }

    .tf-button { &.style-2 { margin-right: 0; } }
  }
  /* === 변경 끝 === */

  &.style-3 {
    padding-top: 10px; padding-bottom: 138px;

    .content-inner {
      padding-top: 131px; margin-right: 163px;
      .sub-heading { margin-bottom: 25px; }

      .counter-wrap {
        margin-bottom: 47px; @include flexitem;

        .tf-counter {
          margin-right: 75px;
          .content { font-size: 44px; line-height: 56px; color:#fff; }
          h6 { font-family:'Poppins'; font-weight:400; font-size:16px; line-height:26px; color:#C2C3C5; }
          &:last-child { margin-right: 0; }
        }
      }
    }

    .image {
      display: unset; position: relative;
      img { margin-right: 0; width: unset; }
      .img { position: relative; z-index: 2; }
      .img-1,.img-2 { position: absolute; top:54%; left:70%; z-index:1; }
      .img-2 { left: 4%; top: 16%; }
    }
  }

  /* ≤1400px에서는 중앙 정렬 */
  @include desktop-1400 {
    flex-wrap: wrap;

    .content-inner {
      width: 90%; max-width: 90%;
      margin: 10px auto 30px auto;
      padding-top: 20px;
      text-align: center;

      .text-typing {
        display: inline-flex; align-items: center; justify-content: center;
        width: fit-content; margin: 0 auto 16px auto; gap: 16px;
      }
      .animate-contain { margin-left: 0; text-align: center; }
    }

    .content-right {
      width: 90%; margin: 0 auto;

      .content-slide {
        justify-content: center;
        .image { &:first-child { margin-left: 0; } }
        @include tablet { height: 330px; }
      }
    }
  }
}

/* slide-in 애니메이션 (기본 텍스트/버튼용 — 마크 메뉴에는 직접 영향 거의 없음) */
.tf-slider {
  position: relative;

  .swiper-slide {
    .heading, .sub-heading, .btn-slider, .counter-wrap, .animate-contain, .animated-text {
      opacity: 0; transform: translateY(200px);
    }
    .sub-heading { transform: translateY(300px); }
  }

  .swiper-slide-active {
    .heading, .sub-heading, .btn-slider, .counter-wrap, .animate-contain, .animated-text {
      opacity: 1; transform: translateY(0); transition-delay: 300ms; visibility: visible;
      -webkit-transition: transform 1000ms ease, opacity 1000ms ease;
      -moz-transition: transform 1000ms ease, opacity 1000ms ease;
      -ms-transition: transform 1000ms ease, opacity 1000ms ease;
      -o-transition: transform 1000ms ease, opacity 1000ms ease;
      transition: transform 1000ms ease, opacity 1000ms ease;
    }
    .sub-heading  { transition-delay: 700ms; }
    .counter-wrap { transition-delay: 850ms; }
    .btn-slider   { transition-delay: 1000ms; }
    .animated-text{ transition-delay: 1200ms; }
  }
}

/* 이미지 공통 */
.tf-slider .image { position: relative; overflow: hidden; }

/* 네온 블루 격자 */
.hologram::before {
  content: ''; position: absolute; inset: 0; pointer-events: none;
  background:
    linear-gradient(90deg, transparent 49%, rgba(0,247,255,0.3) 50%, transparent 51%),
    linear-gradient(0deg,  transparent 49%, rgba(0,247,255,0.3) 50%, transparent 51%);
  background-size: 10px 10px; mix-blend-mode: screen; opacity: 0.3;
}

/* 세로 스캔 라인 */
.hologram::after {
  content: ''; position: absolute; inset: 0; pointer-events: none;
  background: linear-gradient(
    to bottom,
    rgba(0,247,255,0) 0%,
    rgba(0,247,255,0.25) 49%,
    rgba(0,247,255,0.6) 50%,
    rgba(0,247,255,0.25) 51%,
    rgba(0,247,255,0) 100%
  );
  transform: translateY(-120%);
  animation: holo-scan-vert 15s linear infinite;
  mix-blend-mode: screen;
}

@keyframes holo-scan-vert {
  0% { transform: translateY(-120%); }
  33% { transform: translateY(120%); }
  66% { transform: translateY(120%); }
  100% { transform: translateY(-120%); }
}

/* 격자 배경 */
.tf-slider .tf-slider-item { position: relative; overflow: hidden; }
.tf-slider .tf-slider-item::before {
  content: ''; position: absolute; inset: 0; pointer-events: none; z-index: 0;
  background-image:
    linear-gradient(to right, #1e293b 1px, transparent 1px),
    linear-gradient(to bottom, #1e293b 1px, transparent 1px);
  background-size: 40px 40px; opacity: 0.2; animation: slider-grid-pan 60s linear infinite;
}
.tf-slider .tf-slider-item > * { position: relative; z-index: 1; }

@keyframes slider-grid-pan { 0% {background-position:0% 0%;} 100% {background-position:100% 100%;} }

.tf-slider .tf-slider-item.grid-neon::before {
  background-image:
    linear-gradient(to right, rgba(0, 247, 255, 0.25) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(0, 247, 255, 0.25) 1px, transparent 1px);
  opacity: 0.25;
}
.tf-slider .tf-slider-item.grid-fast::before { animation-duration: 25s; }
.tf-slider .tf-slider-item.grid-left::before { animation-name: slider-grid-left; }

@keyframes slider-grid-left { 0% {background-position:0% 0%;} 100% {background-position:-100% 0%;} }

/* X 회전 애니메이션 (예전 로고용) */
@keyframes flipX {
  0%   { transform: rotateY(0deg); }
  20%  { transform: rotateY(360deg); }
  80%  { transform: rotateY(360deg); }
  100% { transform: rotateY(0deg); }
}
.js-flip-letter { display: inline-block; animation: flipX 5s ease-in-out infinite; transform-origin: center; }

/* 모바일 보강 */
@include mobile {
  .tf-slider-item {
    flex-direction: column;
    align-items: center;
    padding: 32px 16px;

    .content-inner {
      width: 100%;
      max-width: 640px;
      margin: 0 auto 16px auto;
      padding-top: 12px;
      text-align: center;

      .heading,
      .sub-heading,
      .animate-contain,
      .btn-slider {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
      }

      .text-typing {
        display: grid !important;
        grid-auto-flow: column;
        justify-content: center;
        align-items: center;
        column-gap: 6px;
        width: max-content;
        margin: 0 auto 12px auto;
      }

      .animate-contain { margin-left: 0; }
      .text-typing .heading { margin: 0; line-height: 0.9; }
      .text-typing .animated-text { transform: translateY(1px); }
    }

    .content-right {
      width: 100%;
      margin: 12px auto 0 auto;

      .content-slide {
        height: auto;
        justify-content: center;

        .image {
          width: 100%;
          max-width: 560px;
          margin-left: 0;
        }
      }
    }
  }
}

@include tablet {
  .tf-slider-item .text-typing { gap: 8px; }
}

.slider-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
  display: block;
}

/* 버튼 정렬 규칙 (기존) */
.btn-slider {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  margin-top: 12px;
}
.brand-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border-radius: 9999px;
  background: rgba(255,255,255,0.08);
  border: 1px solid rgba(255,255,255,0.15);
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  transition: background .2s ease, border-color .2s ease, transform .2s ease;

  img { width: 20px; height: 20px; display: block; }

  &:hover {
    background: rgba(33,231,134,0.15);
    border-color: #21e786;
    transform: translateY(-2px);
  }
}
@media (max-width: 1400px) { .btn-slider { justify-content: center; } }
@media (max-width: 640px)  {
  .btn-slider { gap: 10px; }
  .brand-btn { padding: 8px 12px; }
  .brand-btn img { width: 18px; height: 18px; }
}
/* 데스크톱 간격 미세 조정 */
@media (min-width: 1401px) {
  .tf-slider-item .content-inner .heading:not(.mb0) { margin-bottom: 28px; }
  .tf-slider-item .content-inner .btn-slider { margin-top: 28px; gap: 14px; }
}
@media (min-width: 1800px) {
  .tf-slider-item .content-inner .heading:not(.mb0) { margin-bottom: 32px; }
  .tf-slider-item .content-inner .btn-slider { margin-top: 45px; gap: 16px; }
}
.animated-text span { white-space: pre; } // 공백 유지
@include mobile {
  .tf-slider-item .heading {
    font-size: 45px;
    line-height: 50px;
  }
}

/* =====================================================================
   Minecraft Menu + 거래소(브랜드) 버튼 확장 (슬라이더 내부 전용)
   ===================================================================== */

.tf-slider--mc { /* 이 클래스가 섹션에 붙어 있음 */
  /* Minecraftia 폰트(슬라이더 내부 범위에서만) */
  --mc-font: 'Minecraftia', monospace;

  @font-face {
    font-family: 'Minecraftia';
    src: url(data:font/truetype;base64,...) format('truetype');
    font-display: swap;
  }

  /* 어둡게 오버레이 및 내부 배경 애니(상단 공통 재사용) */
  position: relative;

  // &::before { /* 이미 위에서 정의되어 있지만 범위 명확화 */
  //   content: '';
  //   position: absolute;
  //   inset: 0;
  //   background-color: rgba(0, 0, 0, 0.80);
  //   z-index: 0;
  // }
  // .bg-inner { /* 재정의 없이 그대로 사용 */ }
  // .container-fluid { position: relative; z-index: 2; }
}

/* 가운데 배치 */
.tf-slider--mc .mc-center {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: clamp(24px, 6vw, 80px) 3%;
}

/* 메뉴 루트: 폰트/픽셀 렌더링 */
.tf-slider--mc .mc-root {
  font-family: var(--mc-font);
  color: #ddd;
  text-shadow: 2px 2px #000A;
  image-rendering: pixelated;
}

/* Minecraft Style Menu 그리드 */
.tf-slider--mc .mc-menu {
  --btn-size: clamp(36px, 5.6vw, 56px);
  display: grid;
  grid-template-columns: var(--btn-size) calc(var(--btn-size) * 10) var(--btn-size);
  grid-template-rows: repeat(3, var(--btn-size)) 16px var(--btn-size);
  grid-template-areas:
    ".   first   ."
    ".   second  ."
    ".   third   ."
    ".   .       ."
    "lang fourth .";
  grid-gap: 8px;
  width: calc(var(--btn-size) * 12);
  max-width: 92vw;

  /* 영역 매핑: 첫 3개가 거래소 버튼 */
  & > .mc-button.full:nth-of-type(1) { grid-area: first; }
  & > .mc-button.full:nth-of-type(2) { grid-area: second; }
  & > .mc-button.full:nth-of-type(3) { grid-area: third; }
  & > .double { grid-area: fourth; }
  & > .mc-button.full.lang { grid-area: lang; }

  /* 더블 버튼(Options / Quit) */
  & > .double {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 8px;
  }
}

/* 기본 마크 버튼 (a/button 공통) */
.tf-slider--mc .mc-button {
  height: var(--btn-size);
  width: calc(var(--btn-size) * 10);
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  user-select: none;
  border: 2px solid #000;
  background: #999 url('https://i.ibb.co/rb2TWXL/bgbtn.png') center / cover;
  image-rendering: pixelated;
  padding: 0;
  appearance: none;
  text-decoration: none;

  .title {
    width: 100%;
    height: 100%;
    padding-bottom: .3em;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px; /* 로고와 텍스트 간격 */
    color: #DDD;
    text-shadow: 2px 2px #000A;
    box-shadow: inset -2px -4px #0006, inset 2px 2px #FFF7;

    img { width: 22px; height: 22px; display: block; }
    .label { line-height: 1; }
  }

  &:hover .title {
    background-color: rgba(100, 100, 255, .45);
    text-shadow: 2px 2px #202013CC;
    color: #FFFFA0;
  }
  &:active .title {
    box-shadow: inset -2px -4px #0004, inset 2px 2px #FFF5;
  }

  &.full { width: 100%; height: 100%; }
}

/* 브랜드 변형: 좌측 얇은 칼라 바로 악센트 */
.tf-slider--mc .mc-button.mc-brand {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    left: -2px; top: -2px; bottom: -2px;
    width: 6px;
    background: rgba(255,255,255,0.18);
    pointer-events: none;
  }
  &:hover::before { background: rgba(255,255,255,0.28); }
}

/* 브랜드별 색상 */
.tf-slider--mc .mc-button.mc-brand.is-orca::before { background: rgba(33, 231, 134, 0.35); }
.tf-slider--mc .mc-button.mc-brand.is-dex::before  { background: rgba(128, 229, 227, 0.35); }
.tf-slider--mc .mc-button.mc-brand.is-jup::before  { background: rgba(230, 231, 89, 0.35); }
.tf-slider--mc .mc-button.mc-brand:hover.is-orca::before { background: rgba(33, 231, 134, 0.5); }
.tf-slider--mc .mc-button.mc-brand:hover.is-dex::before  { background: rgba(128, 229, 227, 0.5); }
.tf-slider--mc .mc-button.mc-brand:hover.is-jup::before  { background: rgba(230, 231, 89, 0.5); }

/* 반응형: 작은 화면에서 버튼 크기 살짝 축소 */
@media (max-width: 640px) {
  .tf-slider--mc .mc-menu {
    --btn-size: clamp(36px, 12vw, 48px);
    width: calc(var(--btn-size) * 11.5);
  }
}
/* =====================================================================
   Minecraft Style Text Animation
   ===================================================================== */

#animation {
  position: absolute;
  top: 14%;                 // 메뉴 위로 띄우기
  left: 62%;                // 가로 가운데
  transform: translateX(-50%);
  font-size: 28px;
  color: #f7f700;
  text-shadow: 2px 2px 1px #3f3f00;
  z-index: 100;             // 확실히 제일 위로
}

.element-animation {
  animation: animationFrames linear 0.7s infinite;
}

@keyframes animationFrames {
  0% {
    opacity: 1;
    transform: rotate(-15deg) scale(1);
  }
  50% {
    transform: rotate(-15deg) scale(1.2);
  }
  100% {
    opacity: 1;
    transform: rotate(-15deg) scale(1);
  }
}

/* ================================
   🛠 MC 메뉴: 모바일/태블릿 대응 보강
   (기존 .tf-slider--mc .mc-menu 밑에 추가)
   ================================ */

/* 태블릿: 여백 칸 살짝 축소 + 전체폭 유연화 */
@media (max-width: 1024px) {
  .tf-slider--mc .mc-menu {
    /* 고정폭 대신 뷰포트에 맞추기 */
    width: min(96vw, 760px);
    /* 좌/우 칸을 비율형으로: 화면 줄면 같이 줄어듦 */
    grid-template-columns: minmax(12px, 1fr) minmax(0, 10fr) minmax(12px, 1fr);
  }
}

/* 모바일: 좌/우 칸 더 줄이고, 버튼은 고정폭→가변폭 */
@media (max-width: 768px) {
  .tf-slider--mc .mc-menu {
    --btn-size: clamp(34px, 9vw, 46px);
    width: 94vw;
    grid-gap: 6px;
    grid-template-columns: minmax(10px, 1fr) minmax(0, 9fr) minmax(10px, 1fr);
  }
  /* 버튼 가로폭 고정값 해제 → 가운데 칼럼에 맞춤 */
  .tf-slider--mc .mc-button { width: 100%; }

  /* 안쪽 여백·아이콘·글자 조금씩 줄이기 */
  .tf-slider--mc .mc-button .title { gap: 8px; padding-inline: 8px; }
  .tf-slider--mc .mc-button .title img { width: clamp(16px, 4.2vw, 20px); height: auto; }
  .tf-slider--mc .mc-button .label { font-size: clamp(12px, 3.6vw, 16px); }
}

/* 작은 모바일: 좌/우 칸 더 줄이고 안전영역 고려 */
@media (max-width: 480px) {
  .tf-slider--mc .mc-center {
    padding: clamp(16px, 5vw, 28px) max(3%, env(safe-area-inset-right)) !important;
  }
  .tf-slider--mc .mc-menu {
    --btn-size: clamp(30px, 10.5vw, 42px);
    width: 92vw;
    grid-template-columns: minmax(6px, 1fr) minmax(0, 8.2fr) minmax(6px, 1fr);
  }
  /* 떠 있는 텍스트가 겹치지 않게 위치/크기 축소 */
  #animation { top: 8%; left: 56%; font-size: 18px; }
}

/* 초소형 기기: 더 타이트하게 */
@media (max-width: 360px) {
  .tf-slider--mc .mc-menu {
    grid-template-columns: minmax(4px, 1fr) minmax(0, 7.6fr) minmax(4px, 1fr);
  }
  /* 너무 겹치면 텍스트 숨김 처리 */
  #animation { display: none; }
}